@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --primary: #ac071c;
    --secondary: #677079;
    /* --primary: #315171;
   --secondary: #687d92; */
    /* --primary:#687d92;
    --secondary:#8c8a87; */
    --dark:#1a1b17;
    --shadow:#0d1c27;
    --light:#cacaca;
    --background:#ffffff;
    --dark-screen:#000000e6;
    --light-screen:#fffffa36;
    --colored-screen:#ac071c59;
    --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);
    --title-img:url(/static/media/close-up-concrete-flora-growth.0bb85d18.jpg);
    --display-font: 'Bebas Neue', sans-serif;
    /* 'Unica One', Helvetica, Sans-Serif; */

}
/*black and pink dark mode*/
body.watermelon-light{
    --primary:#ff4a4a;
    --secondary:#4eb310;
    --dark:#0d170e;
    --shadow:#0d1c27;
    --light:#88888870;
    --background:#ffffff;
    --title-img:var(--primary);
    --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);

}
@media only screen{
    body.dark{
       --primary: #ad2133;
       --secondary: #677079;
        --dark:#ffffff;
        --shadow:#000;
        --light:#333333;
        --background:#1a1b17;
        --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);

    }
    body.watermelon-dark{
        --primary:#ff4a4a;
        --secondary:#4eb310;
        --dark:#ffffff;
        --shadow:#333;
        --light:#88888870;
        --background:#212322;
        --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);
        --display-font:'Montserrat', 'Helvetica', sans-serif;
    }
}
@media only screen (prefers-color-scheme: dark){
    /*if prefers dark mode then invert default*/
    :root{
       --primary: #ad2133;
       --secondary: #677079;
        --dark:#ffffff;
        --shadow:#333;
        --light:#88888866;
        --background:#1a1b17;
        --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);

    }
    body.watermelon-light{
        --primary:#ff4a4a;
        --secondary:#4eb310;
        --dark:#ffffff;
        --shadow:#333;
        --light:#88888870;
        --background:#212322;
        --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);

    }
    body.dark{
        --primary: #ac071c;
        --secondary: #677079;
        --dark:#1a1b17;
        --shadow:#0d1c27;
        --light:#88888866;
        --background:#ffffff;
        --colored-screen:#ac071c59;
        --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);
        --title-img:url(/static/media/close-up-concrete-flora-growth.0bb85d18.jpg);

    }
    /*black and pint dark mode*/
    body.watermelon-dark{
        --primary:#ff4a4a;
        --secondary:#4eb310;
        --dark:#0d170e;
        --shadow:#0d1c27;
        --light:#88888870;
        --background:#ffffff;
        --title-img:var(--primary);
        --tile-img:url(/static/media/TileableWatermelon2.f70de74b.png);

    }
}


