body{
    color:#1a1b17;
    color:var(--dark);
    background:#ffffff;
    background:var(--background);
    font-family:'Noto sans', 'Helvetica', sans-serif;
}
a{
    color:var(--primary);
    color:var(--accent,var(--primary));
}


.App .content{
    max-width:1024px;
    margin: 0px 20px 0px 10%;
    padding: 20px 5px;
}

.content-section{
    padding:15px;
    position:relative;
}
.content-section .section-header{
    margin:-15px -15px 0px -15px;
}
.section-header{
    margin:0px -5px;
    padding:5px 10px;
    background:#ac071c;
    background:var(--primary);
}
.section-title{
    -webkit-margin-after:0em;
            margin-block-end:0em;
    -webkit-margin-before:0em;
            margin-block-start:0em;
    font-family:'Bebas Neue', sans-serif;
    font-family:var(--display-font);
    color:#ffffff;
    color:var(--background);
    font-weight: normal;
}
/*transforms*/
.recessed-tile{
    background:url(/static/media/TileableWatermelon2.f70de74b.png);
    background:var(--tile-img);
    background-position:center;
    background-size:8.33%;
    box-shadow: inset 4px 4px 5px #0d1c27;
    box-shadow: inset 4px 4px 5px var(--shadow,black);
    margin-bottom:15px;
}
.recessed-tile > *{
    background:#ffffff;
    background:var(--background);
    box-shadow: 4px 4px 5px #0d1c27;
    box-shadow: 4px 4px 5px var(--shadow,black);
    margin: 5px 10px;
}
.tile-backing{
    background:#c53b3b;
}
.recessed-tile > .row {
    padding: 10px 15px;
}
footer{
    background:#0d1c27;
    background:var(--shadow,black);
    color:#cacaca;
    color:var(--light,white);
    padding:60px 40px;
}
.bio .pfp{
    max-width:150px;
}
@media screen and (max-width:1024px){
    .App .content {
        margin:0px 20px;
    }
}
#about{
    background:#ac071c;
    background:var(--primary);
    -webkit-print-color-adjust: exact;
    color-adjust:exact;
    color:#ffffff;
    color:var(--background);
}
#about p {
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
}
.accept-cookies-popup{
    display:block;
    position:fixed;
    bottom:0px;
    left:0px;
    right:0px;
    padding: 40px;
    background:#000000e6;
    background:var(--dark-screen);
    color:white;
    z-index:9999;
}
.close{
    position:absolute;
    top:5px;
    right:5px;
    width:25px;
    height:25px;
    background:transparent;
    content:"\274c";
    border:none;
}
.close::after{
    position:absolute;
    top:0px;
    right:0px;
    color:white;
    content:"\2717";
    font-size:1.5em;
}

@media only print{
    .cookie-accept-popup{
        display:none;
    }
    footer{
        display:none;
    }
    body{
        font-size:12px;
    }
    .App > .content{
        padding: 0px;
        margin:0px;
    }
    .page-title{
        background-position:center middle;
    }
    .content-section > .section-header{
        padding-bottom:5px;
        position:absolute;
        left:0px;
        top:0px;
        height:40px;
        transform-origin: 0 0;
        transform:rotate(90deg);
        padding:5px;
        background:none;
        color:#ffffff;
        color:var(--background);
        margin:0px;
    }
    .section-title{
        font-size:18px;
        transform:rotate(180deg);
        color:#ffffff;
        color:var(--background,white);
        -webkit-print-color-adjust: exact;
        color-adjust:exact;
    }
    .content-section{
        border:0px solid #ac071c;
        border:0px solid var(--primary);
        border-left-width:40px;
        border-top-width:1px;
        padding:10px;
    }
    .content .content-section{
        margin-bottom:1px;
        border-bottom: 1px solid #ffffff;
        border-bottom: 1px solid var(--background);
    }
}

.hex-pie{
  margin:5px;
}
.hex-name-plate{
  font-family:sans-serif;
  height:80px;
  width:80px;
  background:white;
  background:var(--background,white);
  position:absolute;
  left: 17px;
  top: 29px;
  display:flex;
  flex-direction: column;
  text-align:center;
  justify-content: center;
  border-radius:50%;
  box-shadow:1px 2px 7px black;
  box-shadow:1px 2px 7px var(--shadow,black);
}
.hex-title{
    text-transform: capitalize;
    text-align: center;
}
.hex-info{
    margin-top:-5px;
    color: white;
    color: var(--light,white);
    font-size:80%;
    position: absolute;
    bottom: 10px;
    align-self: center;
}
.hex-hover-box{
    position: absolute;
    height: 200px;
    width: 172px;
    background: var(--dark-screen);
    color: white;
    display:flex;
    align-items:center;
    top: -200px;
    text-align:center;
}
@media only screen{
    .expandable.hex:hover{
        z-index: 1;
    }
    .expandable.hex:hover .hex-hover-box{
        top:0px;
    }
    .expandable.hex:hover .hex-mask{
        left:0px;
        top:0px;
        height: 200px;
        width: 172px;
    }
    .expandable.hex:hover > .hex-mask {
        left: -29px;
        top: -32px;
    }
    .expandable.hex:hover > .hex-mask > .hex-mask > .hex-mask  .hex-mask{
        height: 300px;
    }
    .expandable.hex:hover .hex-name-plate{
        left: 45px;
        top: 58px;
    }
    .expandable.hex:hover .hex-hover-box-text{
        color:white;
    }

}
.hex{
    margin: 5px;
}
.hex .hex-mask{
    background-color: lightgrey;
    background-color: var(--light,lightgrey);
    -webkit-print-color-adjust: exact;
    color-adjust:exact;
    left: 0px;
    top:0px;
}

.hex > .hex-mask{
    left:0px;
    right:0px;
}


.hex-hover-box-text{
    font-size:14px;
    padding:10px;
    color:transparent;
    transition: color 0.1s linear 0.1s;
}


.hex-content{
    height:100%;
    width:100%;
    position:absolute;
    top:0px;
    left:0px;
    overflow-x:hidden;
    overflow-y:hidden;
    -webkit-filter: drop-shadow(1px 2px 3px black);
            filter: drop-shadow(1px 2px 3px black);
}
.hex-pie .hex-left, .hex-pie .hex-right{
    position:absolute;
    width:50%;
    height:100%;
    overflow-x:hidden;
    overflow-y:hidden;
}
.hex-pie .hex-right{
    left: 50%;
}

.hex-left .hex-fill{
    left:100%;
    top:-25%;
    transform-origin: left center;
}
.hex-right .hex-fill{
    right:150%;
    top:-25%;
    transform-origin: right center;
}
.hex-fill{
    position:relative;
    background-color: black !important;
    background-color: var(--primary,black) !important;
    -webkit-print-color-adjust: exact;
    color-adjust:exact !important;
    content:" ";
    width:150%;
    height:150%;
}
.hex-danger > .hex-fill{
    background-color: black;
    background-color: var(--danger,black);
}

.hex-mask{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    position:relative;
    -webkit-clip-path:polygon(50% 0%,100% 25%,100% 75%,50% 100%, 0% 75%, 0% 25%);
            clip-path:polygon(50% 0%,100% 25%,100% 75%,50% 100%, 0% 75%, 0% 25%);
    /* filter:drop-shadow(1px 2px 3px var(--shadow)); */
}
.hex {
    position:relative;
    -webkit-filter: drop-shadow(2px 3px 3px black);
            filter: drop-shadow(2px 3px 3px black);
    -webkit-filter: drop-shadow(2px 3px 3px var(--shadow,black));
            filter: drop-shadow(2px 3px 3px var(--shadow,black));
}
.hex, .hex-mask{
    height:135px;
    width:115px;
}
.hex .hex-mask ,.hex-name-plate{
    transition: all 0.1s ease;
}

.hex-icon-frame{
    margin:5px;
}
.hex-icon-frame .hex-icon{
    top: 30px;
    font-size: 80px;
    position: absolute;
    color:var(--primary);
    -webkit-filter:drop-shadow(2px 2px 3px black);
            filter:drop-shadow(2px 2px 3px black);
    -webkit-filter:drop-shadow(2px 2px 3px var(--shadow,black));
            filter:drop-shadow(2px 2px 3px var(--shadow,black));
}

.hex-icon-frame .hex-content{
    -webkit-print-color-adjust: exact;
    color-adjust:exact !important;
    background-color: grey;
    background-color: var(--primary,grey);
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.hex-icon-frame .hex-icon-title{
    text-shadow: 1px 1px 3px black;
    text-shadow: 1px 1px 3px var(--shadow,black);
    color: white;
    font-weight: 600;
    width: 100%;
    text-align: center;
    text-transform: capitalize;
    position: absolute;
    padding:4px;
}

.hex-row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 15px;
    padding-top: 36px;
}
.hex-row > * {
    margin-top:-32px;
}
/*smallest screen (mobile sm) min width  2 then 1*/
@media only screen{
    .hex-row{
        width:240px;
    }
    .hex-row > .hex:nth-child(2n+1){
        margin-left:67px;
    }
}
/*mobile medium (min 375) 2 and then 1*/
@media  only screen and (min-width: 375px){

    .hex-row > .hex:nth-child(2n+1){
        margin-left:5px;/*cancel last break point*/
    }
    .hex-row{
        width:250px;
    }
    .hex-row > .hex:nth-child(3n){
        margin-left:67px;
    }
}
/*v large phones 3 and then 2? */
@media  only screen and (min-width: 475px){
    .hex-row > .hex:nth-child(3n){
        margin-left:5px;/*cancel prev*/
    }

    .hex-row{
        width:375px;
    }
    .hex-row > .hex:nth-child(5n-1){
        margin-left:67px;
    }
}

/*tablets 4 and then 3*/
@media  only screen and (min-width: 625px){
    .hex-row > .hex:nth-child(5n-1){
        margin-left:5px;/*cancel prev*/
    }

    .hex-row{
        width:500px;
    }
    .hex-row > .hex:nth-child(7n-2){
        margin-left:67px;
    }
}

/* 5 and then 4*/
@media  only screen and (min-width: 775px){
    .hex-row > .hex:nth-child(7n-2){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 630px;
    }
    .hex-row > .hex:nth-child(9n-3){
        margin-left:67px;
    }
}


/* 6 and then 5*/
@media  only screen and  (min-width: 930px){
    .hex-row > .hex:nth-child(9n-3){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 755px;
    }
    .hex-row > .hex:nth-child(11n-4){
        margin-left:67px;
    }
}

/* 7 and then 6 */
@media  only screen and (min-width: 1080px){
    .hex-row > .hex:nth-child(11n-4){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 880px;
    }
    .hex-row > .hex:nth-child(13n-5){
        margin-left:67px;
    }
}
/* 8 and then 7 */
@media only screen and (min-width: 1240px){
    .hex-row > .hex:nth-child(13n-5){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 1005px;
    }
    .hex-row > .hex:nth-child(15n-6){
        margin-left:67px;
    }
}

@media only print {
    .hex-info{
        display:none;
    }
    .hex, .hex-mask{
        width:95px;
        height:115px;
        font-size:12px;
    }
    .hex-row{
        max-width:915px;
        margin:auto;
    }
    .hex-name-plate{
        height: 60px;
        width: 60px;
        left: 18px;
        top: 29px;
    }

    .hex-row > .hex:nth-child(15n-7){
        margin-left:58px;
    }
    .hex-row > * {
        margin-top:-28px;
    }
    .hex-icon-frame .hex-icon{
        font-size:62px;
    }
}

li.experience-item{
    list-style: none;
}
.print ul.experience-items{
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-padding-start:3em;
            padding-inline-start:3em;
}
.print li.experience-item::before{
    content: "\2b21";
    color: var(--dark);
    display: inline-block;
    width: 1.5em;
    margin-left: -1.5em;
}
li.experience-item{
    padding-bottom: 5px;
}
ul.experience-items{
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-padding-start:0em;
            padding-inline-start:0em;
}
.date-range > * {
    display:inline-block;
}
.date-start + .date-end::before{
    content: "\00a0-\00a0";
}
.experience-set{
    /* background:var(--primary,white);
    box-shadow: inset 4px 4px 5px var(--shadow,black); */
    margin-bottom:15px;
    display:block;
    /* padding:20px 10px; */
}
.experience-set> *{
    background:var(--background);
    display:block;
}
.experience .location{
    color:var(--secondary);
}
.experience .location{
    -webkit-margin-before: 0.5em;
            margin-block-start: 0.5em;
    -webkit-margin-after: 0.5em;
            margin-block-end: 0.5em;
    font-size: 20px;
}
.experience-date{
    padding-bottom: 12px;
    text-align: center;
    justify-content: center;
    display:flex;
    min-width:155px;
    width: 13%;
    border-right: 1px solid lightgrey;
    border-right: 1px solid var(--light,lightgrey);
}
.experience-title{
    margin-bottom:8px;
    display:flex;
}
.experience{
    padding:10px;
    page-break-inside: avoid;
}
.experience-position{
    display:flex;
    margin: 0px -10px;
    page-break-inside: avoid;
}
.experience-position > *{
    padding: 5px 10px;
}
.experience-period{
    padding:10px 0px;
}
.experience-set.brief.experience-date{
    border-bottom:none;
    padding-bottom: 0px;
}
@media screen and (max-width:425px){
    .experience-position {
        display:block;
    }
    .experience-position .experience-date{
        border-right:none;
        justify-content: flex-start;
        padding:5px 10px;
    }
}
@media screen and (min-width:425px) and (max-width:768px){
    .experience .date-range .date-end{
        display:block;
    }
    .experience .date-range .date-end::before{
        content:none;
    }
    .experience-date{
        min-width:75px;
        width:75px;
    }
}
@media only print{
    li.experience-item {
        padding-bottom: 0px;
    }
    .experience .location {
        -webkit-margin-before: 0em;
                margin-block-start: 0em;
        -webkit-margin-after: 0em;
                margin-block-end: 0em;
        font-size:16px;
    }
    .experience-set > * {
        padding:0px;
    }
    .experience-date{
        min-width:75px;
        width:75px;
    }
    .experience .date-range .date-end{
        display:block;
    }
    .experience .date-range .date-end::before{
        content:none;
    }
    .experience-title{
        margin:0px;
    }
}

